  --main-color: rgb(0, 0, 128);
  --main-color-light: rgb(065, 105, 225);
  --secondary: rgb(255, 215, 0);
  font-size: 20px;
}

html, body {
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

footer {
  width: 100vw;
  background-color: var(--main-color);
  position: relative;
  bottom: 0;
  margin: 0;
}

footer > * {
  color: white;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

li a {
  text-decoration: none;
  color: inherit;
}

button:hover {
  cursor: pointer;
  font-weight: 900;
}

h1 {
  font-size: 2.5rem;
  letter-spacing: 0.2rem;
}

@media screen and (min-width: 320px) {

  #content > *, footer > * {
    padding: 5px 10px;
  }

  header {
    height: 50px;
    background-color: white;
    box-shadow: 0px 0px 20px black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 10;
  }  

  #hero {
    min-height: calc(100vh - 50px);
    padding-top: 15vh;
    background-color: navy;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    letter-spacing: 0.25rem;
  }

  #hero-img-container {
    background-color: white;
    width: clamp(100px, 45vw, 400px);
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    margin-bottom: 5vh;
    overflow: hidden;
    object-fit: cover;
    box-shadow: 0px 0px 5px white;
  }

  #hero-img-container > img {
    transform: scale(2);
  }

  #hero > h1 {
    text-shadow: 0px 0px 1px white;
  }

  #hero > span > p {
    font-size: 1rem;
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto;
    border-right: 0.15rem solid white; 
    border-left: 0.15rem solid transparent;

    animation: 
      typewriter 2.75s steps(23, end), 
      blink-caret 1s step-end infinite;
  }

  @keyframes typewriter {
    0% {
      width: 0;
    }

    100% {
      width: 100%;
    }
  }

  @keyframes blink-caret {
    0% {
      border-right-color: transparent;
    }

    50% {
      border-right-color: white;
    }
  }

  #overlay {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(1.5px);
    z-index: 20;
  }

  nav {
    display: flex;
  }

  .navbar-container {
    background-color: white;
    height: 100vh;
    position: absolute;
    z-index: 30;
    right: 0;
    top: 0;
    display: none;
  }

  #navbar-close-btn {
    background-color: transparent;
    display: block;
    margin: 5px 0px 0px auto;
  }

  #navbar-close-btn > svg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }

  .navbar-container > button > svg:hover {
    background-color: lightgray;
  }

  #navbar-open-btn {
    width: 40px;
    height: 40px;
    margin-right: 10px;
    background-image: url("https://img.icons8.com/ios-filled/50/menu--v1.png");
    background-size: contain;
    background-color: transparent;
  }

  #navbar-icon:hover{
    cursor: pointer;
  }

  .navbar-container > *{
    padding-top: 0px;
  }

  .navbar-visible {
    animation: expand 0.75s ease-in-out forwards;
    transform-origin: right;
  }

  @keyframes expand {
    0% {
      width: 0;
    }
    100% {
      width: clamp(100px, 60vw, 800px);
    }
  }

  .navbar-hidden {
    animation: retract 0.75s ease-in-out;
    transform-origin: left;
  }

  @keyframes retract {
    0% {
      width: clamp(100px, 60vw, 800px);
    }

    100% {
      width: 0;
    }
  }

  .navbar > li {
    padding: 5px 0px;
    padding-left: 10px;
    background-color: white;
    font-size: 1.5rem;
  }

  .navbar > li:hover {
    font-weight: 900;
    transition: background-color 1s ease;
  }

  .navbar > li::before {
    content: "⟢";
  }

  main > * {
    margin-bottom: 40px;
  }

  main h1 {
    font-size: 2.5rem;
  }

  #carousel-center-title {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin: 2vh 0;
  }

  #project-carousel {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
  }

  #project-carousel > img {
    background-image: cover;
    border-radius: 10px;
  }
  
  .carousel-center {
    width: clamp(50px, 55vw, 800px);
    aspect-ratio: 1 / 1;
    flex-shrink: 0;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
    z-index: 2;
  }

  .carousel-center:hover {
    transform: scale(1.05);
  }

  .carousel-left, .carousel-right {
    width: clamp(50px, 45vw, 700px);
    aspect-ratio: 1 / 1;
    flex-shrink: 0;
    filter: blur(2px);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);

    position: relative;
  }

  .carousel-left {
    left: 25vw;
  }

  .carousel-right {
    left: -25vw;
  }

  #carousel-nav {
    display: flex;
    justify-content: center;
    margin-top: 3vh;
    gap: 20%;
    font-size: 1.15rem;
  }

  .carousel-left-btn, .carousel-right-btn {
    background-color: transparent;
  }

  .carousel-left-btn::before {
    content: "←";
  }

  .carousel-right-btn::after {
    content: "→"
  }

  .newsletter {
    color: white;
    background-color: var(--main-color-light);
    margin: 0px 15px;
    padding: 20px 20px 30px;
    border-radius: 10px;
    text-align: left;
  }

  .newsletter label {
    display: block;
    color: transparent;
  }

  .newsletter form > div {
    display: flex;
    justify-content: center;
    height: 2rem;
  }

  .newsletter input {
    height: 2rem;
    padding-left: 10px;
    border: 0;
    border-radius: 10px 0px 0px 10px;
    width: 70%;
  }

  .newsletter input:focus {
    outline: 3px solid var(--secondary);
    outline-offset: -3px;
  }

  .newsletter button {
    font-weight: 700;
    border-width: 0;
    border-radius: 0px 10px 10px 0px;
    background-color: var(--secondary);
    width: 30%;
  }

  .social-container {
    display: flex;
    justify-content: space-around;
    padding: 10px 25px;
  }

  .social-container > a {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    text-decoration: none;
  }

  .social-icons {
    width: 32px;
    height: 32px;
  }

  .twitter {
    background-color: black;
  }

  .twitter > svg {
    padding: 3px;
  }

  footer > .contact > *:not(:first-child) {
    margin-bottom: 5px;
  }
}

@media screen and (min-width: 481px) {
  #content > *:not(#hero), footer > * {
    padding: 10px 30px;
  }

  header {
    height: 100px;
  }

  .newsletter {
    margin: 80px 60px 20px;
  }

  footer {
    display: grid;
    grid-template: minmax(60px, 100px) 1fr 1fr / 1fr 1fr;
  }

  footer > h1 {
    grid-column: 1 / 3;
  }
}

@media screen and (min-width: 769px) {
  #content > *:not(#hero), footer > * {
    padding: 40px 5vw;
  }

  .carousel-left {
    left: 10vw;
  }

  .carousel-right {
    left: -10vw;
  }

  .newsletter {
    padding: 60px 150px;
  }

}
